(function() {

    angular
        .module('learnApp')
        .controller('explainListController', explainListController);

    explainListController.$inject = ['$scope','ApiService'];
    function explainListController($scope,ApiService) {
        var explainListCtrl = this;

        explainListCtrl.value = {
            explains: [], //
            order: ['', '', '', ''], //
            allExplains:[], //
            myExplains:[], //
            showExplain: [], //1
            showExplainPage:[], //
            currentPage: 1, //
            displayRange: { //
                all: true, //
                my: false //
            },
            flag: {
                empty: false, //0
                error: false //
            }
        };

        explainListCtrl.method = {
            clickOrder:clickOrder, //
            clickExplain:clickExplain, //
            clickRange:clickRange, //
            clickNextPage:clickNextPage, //
            clickBackPage:clickBackPage, //
            clickPage:clickPage //
        };

        //
        require('./../../../css/list.css');
        //
        init();

        /**
         * 
         * @return {[type]} [description]
         */
        function init(){
            /**
             * 
             * @type {[String]} 
             * @type {[String]} 
             * @type {[String]} ID
             */
            ApiService.getExplain($scope.indexCtrl.value.field, $scope.indexCtrl.value.menu,$scope.indexCtrl.value.userId).success(
               function(data) {
                   console.log(data)
                   //
                   if(data.status == 0){
                       //1
                       if(data.data.length > 0){
                           $scope.indexCtrl.value.explainList = data.data; //
                           var explains = $scope.indexCtrl.value.explainList; //
                           explainListCtrl.value.allExplains = explains; //
                           //
                           for(var i = 0; i < explains.length; i++){
                               // 
                               if(explains[i].userId == $scope.indexCtrl.value.userId){
                                   explainListCtrl.value.myExplains.push(explains[i]);
                               }
                           }
                           //()
                           explainListCtrl.value.explains = explainListCtrl.value.allExplains;
                           showExplainCalc(); //
                       }else{
                           explainListCtrl.value.flag.empty = true; //
                       }
                   }else{
                       explainListCtrl.value.flag.error = true; //
                   }
                   $scope.indexCtrl.value.flag.explainListLoading = false; //
               }
           );
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function showExplainCalc(){
            explainListCtrl.value.showExplain = []; //1
            explainListCtrl.value.showExplainPage = []; //
            //
            for(var i = 0; i < Math.ceil(explainListCtrl.value.explains.length / 10); i++) {
              var j = i * 10;
              var p = explainListCtrl.value.explains.slice(j, j + 10);
              explainListCtrl.value.showExplain.push(p);
              explainListCtrl.value.showExplainPage.push(i+1);
            }
        }

        /**
         * 
         * @param  {[Array]} explain []
         * @return {[type]} [description]
         */
        function clickExplain(explain) {
            $scope.indexCtrl.value.showExplain = explain; //
            $scope.indexCtrl.value.flag.showExplain = true; //
        }

        /**
         * 
         * @param  {[String]} value []
         * @return {[type]} [description]
         */
        function clickOrder(value){
            var order = require('./../module/order'); //
            var explains = JSON.stringify(explainListCtrl.value.explains); //
            explainListCtrl.value.explains = JSON.parse(order.order(explains,value)); //
            showExplainCalc(); //
        }

        /**
         * 
         * @param  {[String]} range []
         * @return {[type]} [description]
         */
        function clickRange(range){
            //
            if(range == 'all'){
                explainListCtrl.value.explains = explainListCtrl.value.allExplains; //
            }else{
                explainListCtrl.value.explains = explainListCtrl.value.myExplains; //
            }
            showExplainCalc();//
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickNextPage(){
            //
            if(explainListCtrl.value.currentPage != explainListCtrl.value.showExplainPage.length){
                explainListCtrl.value.currentPage += 1; //
            }
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickBackPage(){
            //1
            if(explainListCtrl.value.currentPage != 1){
                explainListCtrl.value.currentPage -= 1; //
            }
        }

        /**
         * 
         * @param  {Number} page 
         * @return {[type]}      [description]
         */
        function clickPage(page){
            explainListCtrl.value.currentPage = page; //
        }

    }
}());



