(function() {

    angular
        .module('learnApp')
        .controller('explainFormController', explainFormController)
        .directive('explainFormDirective',explainFormDirective);

    explainFormController.$inject = ['$scope'];
    explainFormDirective.$inject = ['$parse'];

    function explainFormController($scope) {
        var explainFormCtrl = this;

        explainFormCtrl.value = {
            explain: {}, //
            flag: {
                alert: false, //
            },
            alert: [], //
        };

        explainFormCtrl.method = {
            clickPreview: clickPreview, //
            clickCodeExplain: clickCodeExplain, //
            dataCheck: dataCheck, //
        };

        //
        require('./../../../css/commonExplain.css');

        init(); //

        /**
         * 
         * @return {[type]} [description]
         */
        function init(){
            //
            if($scope.indexCtrl.value.flag.detailExplain){
                explainFormCtrl.value.explain = $scope.indexCtrl.value.detailExplain; //
            }else{
                explainFormCtrl.value.explain = {}; //
            }
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickPreview(){
            dataCheck(); //
            //
            if(!explainFormCtrl.value.flag.alert){
                var explain = {};
                //
                explain.content = explainFormCtrl.value.explain.content;
                explain.title = explainFormCtrl.value.explain.title;
                explain.imgUrl = explainFormCtrl.value.explain.imgUrl;
                $scope.indexCtrl.value.previewExplain = explain;
                $scope.indexCtrl.value.flag.detailExplain = false; //
                $scope.indexCtrl.value.flag.previewExplain = true; //
            }
        }

        /**
         * 
         * @param  {[file]} img []
         * @return {[type]} [description]
         */
        $scope.$watch("img",function(img){
            if(!img || !img.type.match("image.*")){
                return;
            }
            var reader = new FileReader();
            reader.onload = function(){
                $scope.$apply(function(){
                    explainFormCtrl.value.explain.imgUrl = reader.result; //base64
                    var title = "[]"; //
                    //
                    if(explainFormCtrl.value.explain.content == undefined){
                        explainFormCtrl.value.explain.content = title; //
                    }else{
                        explainFormCtrl.value.explain.content += title; //
                    }
                });
            };
            reader.readAsDataURL(img)
        });

        /**
         * 
         * @param  {[String]} value []
         * @return {[type]} [description]
         */
        function dataCheck(value){
            explainFormCtrl.value.alert = []; //
            //
            if(explainFormCtrl.value.explain.title == undefined){
                explainFormCtrl.value.alert.push('');
            }
            //
            if(explainFormCtrl.value.explain.content == undefined){
                explainFormCtrl.value.alert.push('');
            }
            //
            if(explainFormCtrl.value.alert.length != 0){
                explainFormCtrl.value.flag.alert = true; //
            }else{
                explainFormCtrl.value.flag.alert = false; //
                //
                if($scope.indexCtrl.value.flag.detailExplain){
                    $scope.indexCtrl.value.detailExplain = explainFormCtrl.value.explain; //
                //
                }else{
                    $scope.indexCtrl.value.explainForm = explainFormCtrl.value.explain; //
                }
            }
            explainFormCtrl.value.explain.openRange = value; //
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickCodeExplain(){
            window.open('./#/codeExplain/', 'subwin','width=530 height=400' ); //
        }
    }

    /**
     * [explainFormDirective ]
     * @param  {[type]} $parse [description]
     * @return {[type]}        [descriptions]
     */
    function explainFormDirective($parse){
        return{
            restrict: 'A',
            link: function(scope,element,attrs){
                var model = $parse(attrs.explainFormDirective);
                element.bind('change',function(){
                    scope.$apply(function(){
                        model.assign(scope,element[0].files);
                    });
                });
            }
        };
    }
}());



