(function() {

    angular
        .module('learnApp')
        .controller('showExplainController', showExplainController);

    showExplainController.$inject = ['$scope','$sce'];
    function showExplainController($scope,$sce) {
        var showExplainCtrl = this;

        showExplainCtrl.value = {
            explain: {}, //
            flag: {
                sameUser: false, //
                previewUser: false //
            },
            content: "", //

        };

        showExplainCtrl.method = {
            print: print, //
            clickDetail:clickDetail, //
            finish: finish, //
            clickPreviewComp:clickPreviewComp, //
            clickDelete:clickDelete //
        };

        //
        require('./../../../css/explain/showExplain.css');
        //
        init();

        /**
         * 
         * @return {[type]} [description]
         */
        function init(){
            //
            if($scope.indexCtrl.value.flag.showExplain){
                showExplainCtrl.value.explain = $scope.indexCtrl.value.showExplain; //
            //
            }else{
                showExplainCtrl.value.explain = $scope.indexCtrl.value.previewExplain; //
            }
            //
            if(showExplainCtrl.value.explain.userId == $scope.indexCtrl.value.userId){
                showExplainCtrl.value.flag.sameUser = true;
            }else{
                //ID()
                if(showExplainCtrl.value.explain.userId===undefined){
                    showExplainCtrl.value.flag.previewUser = true;
                }else{
                    showExplainCtrl.value.flag.sameUser = false;
                }
            }
            //
            var explain = require('./../module/markChange'); //
            showExplainCtrl.value.content = explain.markupChange(showExplainCtrl.value.explain.content,showExplainCtrl.value.explain.imgUrl);
            showExplainCtrl.value.content = $sce.trustAsHtml(showExplainCtrl.value.content); //ng-bind-view
        }


        /**
         * 
         * @return {[type]} [description]
         */
        function print(){
           var itemsString = JSON.stringify(showExplainCtrl.value.explain); //
           var itemsStringBase64 = base64url.encode(itemsString); //base64
           var url = './#/explainPrint/' + itemsStringBase64; //URL
           window.open(url); //
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickDetail(){
            $scope.indexCtrl.value.flag.showExplain = false; //
            $scope.indexCtrl.value.flag.detailExplain = true; //
            $scope.indexCtrl.value.detailExplain = showExplainCtrl.value.explain; //
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function finish(){
            //
            if($scope.indexCtrl.value.flag.showExplain){
                $scope.indexCtrl.value.flag.showExplain = false; //
            //
            }else{
                $scope.indexCtrl.value.flag.previewExplain = false; //
            }
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickPreviewComp(){
            $scope.indexCtrl.value.flag.previewExplain = false; //
            $scope.indexCtrl.value.flag.detailExplain = true; //
        }

        /**
         * 
         * @return {[type]} [description]
         */
        function clickDelete(){
            $scope.indexCtrl.value.showExplain = showExplainCtrl.value.explain; //
            $scope.indexCtrl.value.flag.showExplain = false; //
        }
    }
}());



